@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: #f9f9f9;

}
.App {
  display: flex;
  flex-direction: column;
  padding: 3rem;
  font-family: 'Poppins', sans-serif;
}
.App section{
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.todos{
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  padding-left: 0;
}
.item{
  padding: 0 0.5rem;

}
.item-doing{
  background-color: #fdf9c4;
  padding: 0 0.5rem;

}
.item-done{
  text-decoration: line-through;
}
.add-form{
  display: flex;
  align-items: center;
}
input, input:focus-visible{
  border: 2px solid black;
  border-radius: 0;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  -o-border-radius: 0;
  height: 40px;
  font-size: 1rem;
}
.add-btn{
  background-color: #000;
  color: #fafafa;
  height: 46px;
  border: 2px solid black;
  font-family: inherit;
  font-size: 1.2rem;

}
li{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  line-height: 2;
  margin-bottom: 1rem;
}
.item-btn{
  height: 100%;
  border-radius: 0;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  -o-border-radius: 0;
  border: none;
  margin: 0.2rem;
  font-family: inherit;
  font-size: 1rem;
  background-color: #c5c6c8;
}
